.card {
    background-image: url('../../assets/creditCard.png');
    box-shadow: 0px 25px 0px -12px #ffffff4f;
    background-size: cover;
    background-position: top;
    margin: 0 10px;
    border-radius: 35px;
    padding: 20px;
}

.logHolder {
    display: flex;
    justify-content: space-between;
}

.cardlog {
    width: 82px;
    /* height: 35px; */
    font-size: 12px;
    font-weight: 700;
    /* display:flex; */
    text-align: center;
    padding: auto 0;
    padding: 10px 20px;
    background-color: #2b275a;
    color: #fff;
    border-radius: 30px;
    line-height: 17px;
}

.contentData {
    width: 95%;
    margin: 30px 0;
    display: flex;
    flex-direction: column;
}

.cardNo {
    font-size: 12px;
    font-weight: 500;
    color: #757575;
}

.cardCount {
    font-size: 20px;
    font-weight: 800;
    color: #2b275a;
    display: flex;
    gap: 10px;
    justify-content: space-between;
    /* margin-right: 60px; */
}
.justifyBetween {
    display: flex;
    justify-content: space-between;
}

.balance {
    /* display: flex; */
    /* flex-direction : column; */
    /* justify-content: space-between; */
}

.circleDiv {
    display: flex;
}
.circle1 {
    width: 40px;
    height: 40px;
    background-color: #ffffff;
    border-radius: 50px;
    position: absolute;
    margin-left: -26px;
    opacity: 0.4;
}

.circle2 {
    width: 40px;
    height: 40px;
    background-color: #ff8514;
    border-radius: 50px;
}

.grid {
    margin: 10px;
    display: grid;
    grid-template-columns: auto auto;
    gap: 10px;
}
.div12 {
    height: 126px;
    padding: 10px;
    display: flex;
    background-repeat: no-repeat;
}
